*{
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.main_block{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
  /* background-image: linear-gradient(#231942,#5e548e); */
  background-color: #d5bdaf;
}

.main_block div{
  box-shadow: 2px 2px 5px #6d6875;
  border-radius: 10px;
  /* border: 1px solid #8c2f39; */
  background-color: #d6ccc2;
  margin: 20px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
.main_block div:hover{
  transform: scale(1.03);
  
}

.main_block div img{
  width: 300px;
  height: 300px;
  border-radius: 5px;
}
.main_block div:hover img{
  opacity: 70%;
}

.main_block div h1{
  box-shadow: 8px 8px 20px black;
  margin: 10px;
  padding: 5px 20px;
  z-index: -2;
  position: absolute;
  bottom: 20%;
}

.main_block div:hover h1{
  z-index: 2;
}

.main_block div a{
  margin: 15px;
  font-size: 25px;
  text-decoration: none;
  color: black;
  padding: 8px 35px;
  border-radius: 5px;
}
.main_block div a:hover{
  background-color: #b5838d;
}
